
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

export default {
props:['loginname'],
data(){
return{
lists:[]
}
},
components:{
Cell,
Group
},
beforeRouteEnter(to,from,next){
next(function(vm){
vm.$http.get('/topic_collect/'+vm.loginname).then(function(res){

vm.lists=res.data.data
})
})
},
beforeRouteUpdate(to,from,next){
var that=this;
this.$http.get('/topic_collect/'+to.params.loginname).then(function(res){
that.lists=res.data.data
});
next();
}
}



