
import { mapState } from "vuex";
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'

export default {
name: "topiclist",
props: ["name"],
components: {
Tab,
TabItem,
Cell,
Scroller,
Group,
},
computed: {
...mapState(["topics"]),
},

data: function () {
return {
lists: [],
page: 1,
limit: 6, //

pullupconfig: {
content: "",
downContent: "",
upContent: "",
loadingContent: "img...",
},
pulldownfig: {
content: "",
downContent: "",
upContent: "",
loadingContent: "ing...",
},
};
},
methods: {
routeTo(index) {

this.$router.push("/topiclist/" + this.topics[index].name);
},
loadMoreDate() {

this.page = this.page + 1;
var app = this;
this.$http
.get("/topics", {
params: {
tab: this.name,
page: this.page,
limit: this.limit,
},
})
.then(function (res) {




for (var i = 0; i < res.data.data.length; i++) {
app.lists.push(res.data.data[i]);
}
app.$refs.pull.donePullup();






});
},
freshDate() {
var app = this;
this.$http
.get("/topics", {
params: {
tab: this.name,
page: 1,
limit: this.limit,
},
})
.then(function (res) {
app.lists = res.data.data;
app.$refs.pull.donePulldown();
});
},
},



beforeRouteEnter(to, from, next) {

next(function (vm) {
vm.$http
.get("/topics", {
params: {
tab: to.params.name,
page: vm.page,
limit: vm.limit,
},
})
.then(function (res) {

vm.lists = res.data.data;
});
});
},
beforeRouteUpdate: function (to, from, next) {
var that = this;
this.$http
.get("/topics", {
params: {
tab: to.params.name,
page: this.page,
limit: this.limit,
},
})
.then(function (res) {
that.lists = res.data.data;
});
next();
},
};



