
import Group from 'vux/src/components/group/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import {mavonEditor} from 'mavon-editor'
import 'mavon-editor/dist/css/index.css'
import axios from 'axios'
export default {
components:{
Group,
XTextarea,
mavonEditor,
XButton,
Toast
},
data(){
return{
title:'',
content:'',
toastText:'',
toolbars: {
bold: true, // 
italic: true, // 






quote: true, // 
ol: true, // 
ul: true, // 
link: true, // 
imagelink: true, // 





help: true, // 
/* 1.3.5 */



save: false, // eventssave
/* 1.4.2 */

/* 2.1.8 */



/* 2.2.1 */

preview: true 
},
isShowToast:false
}
},
methods:{
addtopic(){
var that = this;
this.$http.post('/topics',{
accesstoken:this.$store.state.accesstoken,
title:this.title,
content:this.content,
tab:'dev'  
}).then(function(res){

that.$router.replace('/topiclist/dev')
}).catch(function(err){


that.isShowToast=true;
that.toastText=''
})
},


uploadImage(pos,file){
var that=this;



var params = new FormData();
params.append('image', file);
axios.post('https://sm.ms/api/v2/upload',params,{

headers: { 'Content-Type' : 'multipart/form-data' },
}).then(function(res){



})
}
}

}



