//

import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);

const store = new Vuex.Store({
    state: {
        topics: [ //tab
            { name: 'all', desc: '' }, //0
            { name: 'good', desc: '' }, //1
            { name: 'share', desc: '' },
            { name: 'ask', desc: '' },
            { name: 'job', desc: '' },
            { name: 'dev', desc: '' }
        ],
        isShowMoreMenus: false, //actionsheet
        // moreMenus: , //
        isLogined: false, //
        unLoginedMenus: {
            login: ''
        },
        loginedMenus: {
            center: '',
            create: '',
            msg: '',
            logout: ''
        },
        loginname: '', //
        accesstoken: '', //
        authorId: '', //id
    },
    getters: {
        moreMenus(state) { //statestorestate
            if (state.isLogined) {
                return state.loginedMenus
            }
            return state.unLoginedMenus
        }
    },
    mutations: {
        //actionsheet
        mutationIsShowMoreMenus(state, payload) {
            state.isShowMoreMenus = payload;
        },
        mutationIsLogined(state) {
            state.isLogined = true;
        },
        //
        mutationLoginname(state, payload) {
            state.loginname = payload;
        },
        //accesstoken
        mutationAccesstoken(state, payload) {
            state.accesstoken = payload;
        },
        //Id
        mutationAuthorId(state, payload) {
            state.authorId = payload;
        },
        //
        mutationLogout(state) {
            state.isLogined = false;
            state.loginname = '';
            state.accesstoken = '';
            state.authorId = '';
        }
    },
    actions: {}
})

export default store;


