
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import {mapMutations} from 'vuex'
export default {
components:{
Group,
XInput,
XButton,
Toast
},
data(){
return{
accesstoken:'',
isShowToast:false,//
toastText:''
}
},
methods:{
...mapMutations(['mutationIsLogined','mutationLoginname','mutationAccesstoken',
'mutationAuthorId']),
dologin(){
var that=this;
this.$http.post('/accesstoken',{
accesstoken:this.accesstoken
}).then(function(res){


if(res.data.success){

that.mutationIsLogined();
that.mutationLoginname(res.data.loginname);
that.mutationAccesstoken(that.accesstoken);
that.mutationAuthorId(res.data.id);

that.$router.push('/user/'+res.data)

that.$router.push('/user/' + res.data.loginname)


window.localStorage.setItem('loginStatus',true)
window.localStorage.setItem('loginname',res.data.loginname)
window.localStorage.setItem('accesstoken',that.accesstoken)
window.localStorage.setItem('author_id',res.data.id)
}
}).catch(function(err){


that.isShowToast=true;
that.toastText='accesstoken'
})
}
}
}



